//ctaBtn
function setCta() {
  $('.ctaBtnModule>.inner').on('click', function () {
    if (!$(this).parent().hasClass('is-active')) {
      $(this).parent().addClass('is-active');
      $(this).parent().find('.content').slideDown();
    }
  });
  $('.ctaBtnModule button').on('click', function (event) {
    if ($(this).parents('.ctaBtnModule').hasClass('is-active')) {
      $(this).parents('.ctaBtnModule').removeClass('is-active');
      $(this).parent().find('.content').slideUp('fast');
      event.stopPropagation();
    }
  });
  $('.ctaBtnModule>.inner').hover(
    function () {},
    function () {
      $(this).parent().removeClass('is-active');
      $(this).parent().find('.content').slideUp('fast');
    }
  );
}

//microCMS
function setMicroCms() {
  const API_KEY = '2daTwkL0jF97p8B658xPXKnA8lMRwE9fArL9'; // APIキー
  const ENDPOINT = 'https://clainel.microcms.io/api/v1/news'; // microCMSのエンドポイント
  const TARGET_SELECTOR = '.news_list'; // 表示先の要素のセレクター
  const MAX_COUNT = 10; // 表示するニュースの最大数

  // 日付をフォーマットする関数
  function formatDate(date) {
    return `${date.getFullYear()}.${date.getMonth() + 1}.${date.getDate()}`;
  }

  // ニュースデータからHTMLを生成する関数
  function createNewsHtml(news) {
    const baseDate = news.publishedAt.split('T')[0];
    const newsDate = formatDate(new Date(news.publishedAt));
    const newsTitle = news.title;
    const newsUrl = news.url;

    let newsHtml = '';

    if (newsUrl) {
      newsHtml = `
            <article class="itemStyle_06">
              <a href="${newsUrl}" target="_blank">
                <time class="update" datetime="${baseDate}">${newsDate}</time>
                <h3 class="ttl">
                  ${newsTitle}
                </h3>
              </a>
            </article>
            `;
    } else {
      newsHtml = `
            <article class="itemStyle_06">
              <div>
                <time class="update" datetime="${baseDate}">${newsDate}</time>
                <h3 class="ttl">
                  ${newsTitle}
                </h3>
              </div>
            </article>
`;
    }

    return newsHtml;
  }
  // ニュースを取得して表示する関数
  function fetchAndDisplayNews() {
    fetch(ENDPOINT, {
      headers: {
        'X-MICROCMS-API-KEY': API_KEY,
      },
    })
      .then((response) => response.json())
      .then((json) => {
        const newsData = json.contents;
        const targetElement = document.querySelector(TARGET_SELECTOR);
        const count = Math.min(MAX_COUNT, newsData.length);

        for (let i = 0; i < count; i++) {
          const newsHtml = createNewsHtml(newsData[i]);
          targetElement.insertAdjacentHTML('beforeend', newsHtml);
        }
      })
      .catch((error) => {
        console.error('Error fetching news:', error);
      });
  }

  // ニュースの取得と表示
  fetchAndDisplayNews();
}

// /////フォーム制御
function setForm() {
  const form = document.getElementById('form');
  const targetButton = document.querySelector('.btnkakunin');
  const triggerCheckbox = document.querySelector('input[name="form_data[privacy_policy]"]');

  targetButton.disabled = true;
  targetButton.classList.add('is-inactive');
  triggerCheckbox.checked = false;

  form.addEventListener('input', update);
  form.addEventListener('change', update);

  function update() {
    const isRequired = form.checkValidity();

    if (isRequired) {
      targetButton.disabled = false;
      targetButton.classList.remove('is-inactive');
      targetButton.classList.add('is-active');
      return;
    } else {
      targetButton.disabled = true;
      targetButton.classList.remove('is-active');
      targetButton.classList.add('is-inactive');
      return;
    }
  }

  $('.input-tel').attr('pattern', '\\d*');
  $('.input-tel').attr(
    'onkeyup',
    'if(event.keyCode==9||event.keyCode==16) return; this.value=this.value.replace(/[^0-9]+/i,"")'
  );
}

