function setSlider_04() {
  let currentSlide;
  if ($('#wrapper').hasClass('sepia-mood')) {
    currentSlide = '0';
  } else if ($('#wrapper').hasClass('moon-brown')) {
    currentSlide = '1';
  } else if ($('#wrapper').hasClass('classic-brown')) {
    currentSlide = '2';
  } else if ($('#wrapper').hasClass('lady-mauve')) {
    currentSlide = '3';
  }
  const mySwiperMain = new Swiper('.carouselStyle_04 .swiper-main', {
    slidesPerView: 1,
    // initialSlide: currentSlide,
    centeredSlides: true,
    spaceBetween: 14,
    watchSlidesProgress: true,
    threshold: 10,
    loop: true,
    loopAdditionalSlides: 5,
    speed: 1000, // スライドアニメーションのスピード（ミリ秒）
    autoplay: {
      // 自動再生
      delay: 4000,
      disableOnInteraction: false, // 矢印をクリックしても自動再生を止めない
      waitForTransition: false, // アニメーションの間も自動再生を止めない（最初のスライドの表示時間を揃えたいときに）
    },
    // effect: 'fade',
    pagination: {
      el: '.swiper-pagination',
      clickable: true, // クリック有効化
      type: 'fraction',
    },
    on: {
      // イベント名：イベントが発生したときの処理（イベントハンドラ）を指定
      init: function () {
        // 指定のスライドに移動（例: スライド3に移動）
        var targetSlideIndex = currentSlide; // 0ベースのインデックス
        this.slideTo(targetSlideIndex, 0, false);
      },
    },
  });
  const swiperPagination = document.querySelector('.swiper-pagination');
  // 直下のテキストノードを変更
  if (swiperPagination) {
    const textNode = swiperPagination.childNodes[1];
    if (textNode.nodeType === Node.TEXT_NODE) {
      textNode.nodeValue = ''; // 新しいテキストに変更
    }
  } // $('.swiper-pagination').text('');
}
